<template>
  <transition tag="div" name="fade">
    <div class="click-heart" v-if="hearts.length > 0">
      <div
        class="heart"
        v-for="item in hearts"
        :key="item.left + item.top"
        :style="{ left: item.left, top: item.top }"
      ></div>
    </div>
  </transition>
</template>
<script>
export default {
  data() {
    return {
      hearts: [],
    }
  },
  mounted() {
    document.addEventListener('click', (e) => {
      clearTimeout(this.timer)

      let number = Math.floor(Math.random() * (10 - 5 + 1) + 5)
      this.hearts = []
      for (let i = 0; i < number; i++) {
        let posX = Math.floor(Math.random() * (100 + 1))
        let posY = Math.floor(Math.random() * (200 + 1))
        this.hearts.push({
          left: e.clientX + posX + 'px',
          top: e.clientY + posY + 'px',
        })
      }
      this.timer = setTimeout(() => {
        let i = number
        while (i > -1) {
          this.hearts.splice(i, 1)
          i--
        }
      }, 1000)
    })
  }
}
</script>

<style scoped>
.heart {
  width: 10px;
  height: 10px;
  position: absolute;
  background: red;
  transform: rotate(45deg);
  opacity: 1;
}
.heart::before {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  background: red;
  border-radius: 50% 0 0 50%;
  left: -50%;
  top: 0;
}
.heart::after {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  background: red;
  border-radius: 50% 50% 0 0;
  right: 0px;
  top: -50%;
}
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s;
}
.fade-enter, .fade-leave-to{
  opacity: 0;
}
</style>
